import React from 'react';

export default function TableRow(props){
    return (<div style={styles.tableRow}>
        <div style={styles.rightCol}>{props.name}:</div>
        <div style={styles.leftCol}>{props.value}</div>
    </div>) 
}

const styles ={
    tableRow: {
        padding: '20px',
        display: 'flex',
        flexDirection: 'row',
        width: '80%'
    },
    rightCol: {
        textAlign: 'right',
        width: '30%',
        paddingRight: 25
    },
    leftCol: {
        textAlign: 'left',
        width: '70%',
        display: 'block',
        paddingLeft: 25
    }
}