import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import React from "react";
import Slider from "react-slick";
import ImageContainer from './ImageContainer';


export default function Slideshow(props){

  const settings = {
    dots: false,
    adjustHeight: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1
  };
    return (<Slider {...settings} className="detail-page-carousel">
            {props.images.map((image, index) => {
                var description = props.descriptions[index] ?? '';
                return <ImageContainer source={image} text={description} edit={false}/>
            })}
        </Slider>)
}

